import naviTitle from "@/components/navi-title.vue";
// import lineChart from '@/components/u-charts/pages/ucharts-demo/pie/pie.vue';

import starCompanyItem from "./components/star-company-item.vue"
import reportItem from "./components/report-item.vue"

import Tabs from '@/components/tabs/tabs.vue'
import TabPane from '@/components/tabs/tabPane.vue'

import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaLineA=null;

/*下面是服务器返回的数据格式*/
var Data={
		"LineA": {
		  "categories": ["2012", "2013", "2014", "2015", "2016", "2017"],
		  "series": [{
			"name": "成交量A",
			"data": [35, 8, 25, 37, 4, 20]
		  }, {
			"name": "成交量B",
			"data": [70, 40, 65, 100, 44, 68]
		  }, {
			"name": "成交量C",
			"data": [100, 80, 95, 150, 112, 132]
		  }]
		}
	}

export default {
	components: {
		naviTitle,
		// lineChart,
		starCompanyItem,
		reportItem
	},
	data() {
		return {
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			serverData:'',
			screenHeight: 0,
			animationData: {
				0: {},
				1: {},
				2: {}
			},
			title: '0',
			indicatorDots: false,
			autoplay: false,
			previousMargin: uni.upx2px(82) + 'px',
			nextMargin: uni.upx2px(82) + 'px',
			circular: true,
			zoomParam: 1.0,
			swiperCurrentIndex: 0,
			data: [],
			max: 0,
			imgs: [
				'../../static/1.jpg',
				'../../static/2.jpg',
				'../../static/3.jpg'
			]
		}
	},
	computed: {

	},
	onLoad() {
		_self = this;
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData(){
			_self.serverData=Data;
			let LineA={categories:[],series:[]};
			//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
			LineA.categories=Data.LineA.categories;
			LineA.series=Data.LineA.series;
			_self.showLineA("canvasLineA",LineA);
		},
		showLineA(canvasId,chartData){
			canvaLineA=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'line',
				fontSize:11,
				legend:false,
				dataLabel:false,
				dataPointShape:true,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					type:'grid',
					gridColor:'#CCCCCC',
					gridType:'dash',
					dashLength:8
				},
				yAxis: {
					gridType:'dash',
					gridColor:'#CCCCCC',
					dashLength:8,
					splitNumber:5,
					min:10,
					max:180,
					format:(val)=>{return val.toFixed(0)+'元'}
				},
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					lineStyle: 'straight'
				}
			});
			
		},
		change(e) {
			this.swiperCurrentIndex = e.detail.current;
			this.title = e.detail.currentItemId;
			for (let key in this.animationData) {
				if (e.detail.currentItemId == key) {
					// this.animation.scale(this.zoomParam).step();
					// this.animationData[key] = this.animation.export();
				} else {
					// this.animation.scale(1.0).step();
					// this.animationData[key] = this.animation.export();
				}
			}
		}
	}
}
